.contacts {
    background-color: $pageBg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 86px 50px 113px;
    &__head {
        display: none;
    }
    &__box {
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 480px;
        width: 100%;
        justify-content: center;
    }
    &__container {
        background-color: $white;
        box-shadow: 0px 8px 30px $contactsBoxShadow;
        border-radius: 24px;
        padding: 43px 20px;
    }
    &__title {
        @include font-mixin('DM Sans', 500, 12px, 1.86, 0.04em, $white);
        border-radius: 6px;
        text-align: center;
        padding: 2px 25px;

        &--cafe {
            background-color: $CoffeeBG;
        }
        &--foodtruck {
            background-color: $lime;
        }
    }
    &__city {
        @include font-mixin('DM Sans', 500, 12px, 1.86, 0.04em, $body);
        padding: 25px 0 10px;
        display: block;
    }
    &__description {
        @include font-mixin('DM Sans', 500, 12px, 1.85, normal, $text);
        padding-bottom: 25px;
        border-bottom: 1px solid $gray;
    }
    &__opening-hours {
        padding: 25px 0;
        gap: 25px;
        display: flex;
        flex-direction: column;
    }
    &__weekdays {
        @include font-mixin('DM Sans', 500, 12px, 1.86, normal, $body);
        padding-bottom: 5px;
    }
    &__hours {
        @include font-mixin('DM Sans', 500, 12px, 1.86, normal, $brand-pink);
    }
    &__details {
        border-top: 1px solid $gray;
        padding-top: 25px;
    }
    &__info {
        @include font-mixin('DM Sans', 500, 12px, 1.5, normal, $text);
    }
    &__buttons {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 54px;
        gap: 10px;
    }
}
@media (min-width: 768px) {
    .contacts {
        padding: 119px 36px 158px;
        &__box {
            flex-direction: row;
            gap: 30px;
            max-width: unset;
        }
        &__content {
            padding: 50px 32px 50px 31px;
        }
        &__title {
            font-size: 16px;
            &--cafe {
                padding: 5px 25px;
            }
            &--foodtruck {
                padding: 5px 25px;
            }
        }
        &__city, &__weekdays, &__hours, &__info &__description {
            font-size: 14px;
        }
        &__buttons {
            flex-direction: row;
        }
    }

}
@media (min-width: 1200px) {
    .contacts {
        background-image: url(/src/images/sectionbg21.png);
        background-position: center;
        background-repeat: no-repeat;
        &__box {
            gap: 50px;
        }
    }
}
        